.pokemon_btn:hover {
  box-shadow: 5px 5px;
}


/* Game component */
.Game {
  display: flex;
  flex-direction: column;
}

.game_header {
  display: flex;
  justify-content: space-between;
  padding: 10px 25px;
}

.gameboard {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  margin-top: 25px;
}

.hide_card {
  visibility: hidden;
}

.Card {
  border: solid 3px black;
  border-radius: 10px;
  background-color: #f5f5f5;
  width: 300px;
  height: 150px;
  position: relative;
  /* transform: rotateX(180deg); */
  transform: scaleX(-1);
  transition: 0.3s ease-in;

}

.Card.back_card {
  content: url("whos_that_pokemon.jpg");
  transform: rotateX(360deg);
}

/* GameOver Page */
.GameOver {
